@font-face {font-family: "Star Jedi"; src: url("//db.onlinewebfonts.com/t/0c724f6aa457310440cf8949c615cbd7.eot"); src: url("//db.onlinewebfonts.com/t/0c724f6aa457310440cf8949c615cbd7.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/0c724f6aa457310440cf8949c615cbd7.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/0c724f6aa457310440cf8949c615cbd7.woff") format("woff"), url("//db.onlinewebfonts.com/t/0c724f6aa457310440cf8949c615cbd7.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/0c724f6aa457310440cf8949c615cbd7.svg#Star Jedi") format("svg"); }
.home__container {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 0px 20px;
}
.starWarsFontLarge {
    color: #feda4a;
    font-family: 'Star Jedi', 'Pathway Gothic One', sans-serif;
    font-size: 3em;
    font-weight: 600;
    letter-spacing: 6px;
    line-height: 150%;
    text-align: center;
}
.starWarsFontMedium {
    color: #feda4a;
    font-family: 'Pathway Gothic One', sans-serif;
    font-size: 1em;
    font-weight: 600;
    letter-spacing: 6px;
    line-height: 150%;
    text-align: center;
}
.header {
margin-bottom: 20px;
background: rgb(93,93,93);
background: linear-gradient(0deg, rgba(93,93,93,0) 0%, rgba(65,0,0,1) 62%, rgba(135,0,0,1) 100%);
height: 150px;
}
.tab {
    margin: 0px !important;
    padding: 0px !important;
    max-width: 100%;

}
.tabs {
    color: #feda4a !important;
    font-size: 1.3em !important;
    font-weight: 600 !important;
    text-transform: none !important;
    letter-spacing: 6px;
    text-align: center;
    margin: 12px !important;
    margin-bottom: -12px !important;
    padding: 0px !important;
}
.blade {
    height: 10px;
    max-width: calc(100% -  100px);
    background: white;
    box-shadow: 0 0 12px rgba(255, 0, 0, 1);
    margin-top: -27px;
    margin-left: 90px;
    border-radius: 20px;
    -webkit-transition: 1s ease-in-out;
    -moz-transition: 1s ease-in-out;
    -o-transition: 1s ease-in-out;
    transition: 1s ease-in-out;

}
.hilt {
    height: 40px;
}
.tabHolder {
    width: 100%;
    max-width: 400px;
    display: flex;
    justify-content: space-between;
}
.crawlContainer {

    max-width: 500px;
    perspective: calc(100vh * 0.8);
  }
.crawl {
    margin-top: -240px;
    color: #feda4a;
    font-size: 1.4em;
    font-weight: 600;
    letter-spacing: 3px;
    line-height: 150%;
    text-align: center;
    height: 200%; 
    overflow: hidden;  
    transform-origin: 50% 100%; 
    transform: rotate3d(1, 0, 0, 45deg);
  }